import React,{useEffect} from 'react';
import store from 'src/store';
import * as allFound from 'src/actions/testingActions';
import {updateTesting} from "../testingActions";

const updateTestingValue = "hi";


const TestingActionsTest = () => {
    useEffect(()=>{
        store.dispatch(updateTesting({
            numItems:Object.keys(allFound).length,
            updateTestingValue:updateTestingValue,
            updateTesting:updateTesting(updateTestingValue)
        }));
        console.dir(allFound);
    },[]);

    return (
        <div>
            <div>
                No Buttons Needed

            </div>
        </div>
    )
};

export default TestingActionsTest;
